.container {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 50px;
}
.title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: wheat;
  margin-top: 30px;
  font-size: 50px;
}
.span {
  color: rgb(44, 185, 255);
  margin-left: 10px;
}
.reset {
  width: 240px;
  height: 67px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 50px;
  background: #1f3540;
  font-size: 26px;
  color: rgb(44, 185, 255);
}
.box {
  display: flex;
  background-color: #1f3540;
  width: 130px;
  height: 130px;
  border: 4px solid #0f1b21;
  cursor: pointer;
  color: yellow;
  border-radius: 12px;
  box-shadow: yellow 1.5rem 1.5rem 3rem 0rem;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
}
.board {
  height: auto;
  width: 400px;
}
.currentStatus {
  color: rgb(233, 233, 154);
  /* margin-bottom: 20px; */
}
.reset:hover {
  color: goldenrod;
  background-color: #1c6f99;
}
